.page-footer__wrapper {
  padding-right: 234px;
  padding-left: 234px;
}

.page-footer-top {
  display: flex;
  justify-content: space-between;
  padding-bottom: 85px;

  border-bottom: 2px solid #ececec;
}

.page-footer-top__menu {
  .reset;

  display: flex;
  justify-content: space-between;
  width: 901px;
  padding-top: 92px;
}

.page-footer-top__menu--link {
  display: block;

  font-size: 18px;
  line-height: 18px;
  letter-spacing: 0.6px;
  color: #777777;
  text-transform: uppercase;

  &:hover {
    text-decoration: underline;
    text-decoration-color: #777777;

    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }
}

.form-copyright__wrapper {
  padding-top: 71px;
}

.page-footer-top__form {
  display: flex;
}

.footer-top-form__input {
  width: 410px;

  border: 1px solid #ececec;
  box-shadow: none;
}

.page-footer-top__copyright {
  .reset;

  display: flex;
  justify-content: space-between;
  width: 477px;
  padding-top: 57px;
}

.page-footer-top__copyright--link {
  font-size: 14px;
  font-weight: 300;
  line-height: 14px;
  letter-spacing: 0.2px;
  color: #888888;
  text-decoration: underline;
  text-decoration-color: #888888;

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }
}

.page-footer-bottom {
  display: flex;
  justify-content: flex-end;
  padding-top: 40px;
  padding-bottom: 43px;
}
