.promo {
  height: 1166px;

  background-image: url("../img/number-3.png"), url("../img/bckgrnd-2.png");
  background-repeat: no-repeat;
  background-size: 514px 1107px, 1920px 626px;
  background-position: 1410px 0, 0 545px;
}

.promo__wrapper {
  min-width: 1626px;
  margin: 0 auto;
  padding: 0 147px;
}

.promo__description {
  position: relative;

  margin: 0;
  padding-top: 78px;
  padding-bottom: 37px;
  padding-left: 63px;

  font-size: 30px;
  font-weight: 600;
  line-height: 0.8;
  letter-spacing: 1.1px;
  text-transform: uppercase;
}

.promo__description::before {
  content: "";

  position: absolute;
  top: 90px;
  left: -150px;

  width: 129px;
  height: 3px;

  background-color: #222222;
}

.promo__text {
  padding-bottom: 96px;
  padding-left: 63px;

  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 0.7px;
  color: #95cac5;
  text-transform: uppercase;
}

.promo__title-wrapper {
  position: absolute;
  top: 300px;
  left: 330px;
  z-index: 9;
}

.promo__title {
  width: 880px;
  margin: 0;

  font-family: "Playfair Display";
  font-size: 76px;
  font-weight: 900;
  font-style: italic;
  line-height: 1.32;
  letter-spacing: 2.7px;
  text-transform: uppercase;
  color: #ffffff;

  text-shadow: 0 8px 35px rgba(0, 0, 0, 0.2);
}

.title-top {
  text-align: left;
}

.title-bottom {
  text-align: right;
}

.promo__contacts {
  display: flex;
  justify-content: space-between;
  height: 392px;
  margin-top: 220px;
  margin-left: -90px;

  font-size: 21px;
  line-height: 1.619;

  writing-mode: tb-rl;
  transform: rotate(180deg);
}

.promo__contacts a {
  color: rgb(51, 51, 51);

  &:hover {
    text-decoration: underline;
    text-decoration-color: #777777;
    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }
}

.promo__images {
  position: relative;

  display: flex;
  justify-content: space-between;
  margin-top: 97px;
}

.img-1 {
  position: absolute;
  top: 43px;
  left: 0;
  z-index: 4;

  margin-bottom: 286px;

  display: block;
  width: 534px;
  height: 594px;

  box-shadow: 0 31px 57.66px 4.34px rgba(0, 0, 0, 0.22);

  &:hover {
    z-index: 10;

    animation: hover-effect 0.6s forwards;
  }

  &:active {
    opacity: 0.5;
  }
}

