.promo__btn-wrapper {
  position: absolute;
  top: 0;
  left: 1410px;
}

.promo__btn {
  position: absolute;

  display: block;
  width: 223px;
  padding-top: 16.5px;
  padding-bottom: 16.5px;

  line-height: 1.5;
  letter-spacing: 0.6px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;

  background-color: #95cac5;
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.06);
  border: solid 2px #95cac5;

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }
}

.promo__btn svg {
  fill: #ffffff;
}
