.page-header {
  height: 250px;
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 1920px;
  margin: 0 auto;
  padding: 0 150px;
  padding-top: 124px;
}

.page-header__toggle {
  background-color: transparent;
  border: none;

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }
}

.page-header__logo--link {
  position: relative;

  display: block;

  font-size: 24px;
  line-height: 1;
  letter-spacing: 7.2px;
  text-transform: uppercase;

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }
}

.page-header__logo--link svg {
  position: absolute;
  top: -10px;
  left: -40px;
}

.social {
  display: flex;
  justify-content: space-between;
  width: 170px;
}

.social__link {
  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }
}
