.popular {
  position: relative;

  padding-top: 123px;
}

.popular::before {
  content: "";

  position: absolute;
  top: 0;
  right: 958px;

  width: 4px;
  height: 65px;

  background-color: #222222;
}

.popular__title {
  .title-second;

  padding-bottom: 21px;
}

.popular__subtitle {
  display: block;
  margin-bottom: 76px;

  font-family: "PlayfairDisplay";
  font-size: 17px;
  font-weight: bold;
  font-style: italic;
  line-height: 1.41;
  letter-spacing: 0.4px;
  color: #95cac5;
  text-align: center;
}

.popular__wrapper {
  position: relative;

  display: flex;
  justify-content: space-between;
  padding-right: 365px;
  padding-left: 289px;
}

.top-line {
  margin-bottom: 60px;
}

.top-line::after {
  content: "";

  position: absolute;
  top: 880px;
  left: 289px;

  width: 468px;
  height: 3px;

  background-color: #ececec;
}

.top-line::before {
  content: "";

  position: absolute;
  top: 770px;
  right: 365px;

  width: 468px;
  height: 3px;

  background-color: #ececec;
}

.popular-item {
  position: relative;
}

.popular-item__copyright {
  position: absolute;

  display: flex;
  justify-content: space-between;
}

.copyright-bttm-tp {
  left: -30px;

  writing-mode: tb-rl;
  transform: rotate(180deg);
}

.copyright-tp-bttm {
  right: -30px;

  writing-mode: tb-rl;
}

.copyright-small {
  top: 93px;
}

.popular-item__copyright--info {
  font-size: 14px;
  line-height: 1.714;
  color: #333333;
  text-transform: uppercase;
}

.copyright__magazine {
  padding-top: 20px;

  color: #95cac5;
}

.popular-item__img {
  margin-bottom: 41px;

  &:hover {
    animation: hover-effect 0.6s forwards;
  }

  &:active {
    opacity: 0.5;
  }
}

.img-big {
  width: 670px;
  height: 740px;
}

.img-small {
  width: 478px;
  height: 554px;
  margin-top: 93px;
}

.popular-item__text {
  margin: 0;

  font-size: 18px;
  line-height: 1.33;
  color: #333333;
  text-transform: uppercase;
}
