.upcoming {
  padding-top: 151px;
  padding-right: 310px;
  padding-bottom: 151px;
  padding-left: 220px;

  background-color: #f8f8f8;
}

.upcoming__wrapper {
  position: relative;

  display: flex;

  background-color: #ffffff;
}

.upcoming__info {
  padding-top: 136px;
  padding-bottom: 140px;
  padding-left: 120px;

  background-image: url("../img/number-5.png");
  background-repeat: no-repeat;
  background-position: 82px 74px;
}

.upcoming__info--title {
  margin: 0;
  padding-bottom: 41px;

  font-size: 22px;
  font-weight: 600;
  line-height: 1.27;
  letter-spacing: 0.8px;
  text-transform: uppercase;
}

.upcoming__info--subtitle {
  width: 331px;
  margin: 0;
  padding-bottom: 45px;

  font-family: "Playfair Display";
  font-size: 26px;
  font-style: italic;
  line-height: 1.69;
  letter-spacing: 0.4px;
}

.upcoming__info--text {
  width: 476px;
  margin: 0;

  font-family: "Hind";
  font-weight: 300;
  line-height: 1.88;
  letter-spacing: 0.2px;
  color: #777777;
}

.upcoming__img {
  position: absolute;
  top: 95px;
  right: -90px;

  width: 796px;
  height: 448px;

  box-shadow: 0 5px 35px 0 rgba(0, 0, 0, 0.08);

  &:hover {
    animation: hover-effect 0.6s forwards;
  }

  &:active {
    opacity: 0.5;
  }
}

.upcoming__img::after {
  content: "";

  position: absolute;
  top: 200px;
  right: 350px;

  width: 66px;
  height: 66px;

  background-color: #ffffff;
  border-radius: 50%;
}

.upcoming__img::before {
  content: "";

  position: absolute;
  top: 220px;
  right: 365px;
  z-index: 2;

  width: 0;
  height: 0;

  border-top: 13px solid transparent;
  border-left: 26px solid #222222;
  border-bottom: 13px solid transparent;
}
