.subscribe {
  margin-top: 121px;
  padding-bottom: 79px;

  background-image: url("../img/bckgrnd-2.jpg");
  background-repeat: no-repeat;
  background-size: 1920px 365px;
}

.subscribe__title {
  margin: 0;
  padding-top: 109px;
  padding-bottom: 20px;

  font-size: 22px;
  font-weight: 600;
  letter-spacing: 0.8px;
  color: #333333;
  text-transform: uppercase;
  text-align: center;
}

.subscribe__text {
  margin: 0;
  margin-bottom: 37px;

  font-family: "Playfair Display";
  font-style: italic;
  line-height: 1.88;
  letter-spacing: 0.9px;
  color: #555555;
  text-align: center;
}

.form__wrapper {
  display: flex;
  padding-left: 668px;
}

.form__input {
  .placeholder;

  font-family: "Hind";
  line-height: 1.88;

  width: 510px;
  padding-top: 15px;
  padding-left: 31px;
  padding-bottom: 10px;

  box-shadow: 0 27px 24px 0 rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  border: none;
}

.form__btn {
  width: 66px;
  height: 56px;

  margin-left: -4px;
  padding: 0;

  background-color: #83bbb6;
  border: none;
  outline: none;

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.2;
  }
}
