.news {
  position: relative;

  margin-bottom: 143px;
}

.news::before {
  content: "";

  position: absolute;
  right: 958px;

  width: 4px;
  height: 90px;

  background-color: #222222;
}

.news__title {
  .title-second;

  padding-top: 160px;
  padding-bottom: 61px;
}

.news__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  padding-right: 347px;
  padding-left: 344px;
}

.news-item {
  box-sizing: content-box;
  width: 367px;
  padding-right: 64px;

  &:hover {
    animation: hover-effect 0.6s forwards;
  }

  &:active {
    opacity: 0.5;
  }
}

.news-item:nth-child(3n+3) {
  padding-right: 0;
}

.news-item__img {
  font-size: 0;
  line-height: 0;
}

.news-item__info {
  padding-top: 30px;
  padding-right: 60px;
  padding-bottom: 42px;
  padding-left: 34px;

  background-color: #f4f4f4;
  box-shadow: 0 5px 21px 0 rgba(0, 0, 0, 0.04);
}

.news-item__info--title {
  margin: 0;
  margin-bottom: 26px;

  font-size: 18px;
  line-height: 1.78;
  letter-spacing: 0.3px;
  font-weight: 600;
}

.news-item__info--text {
  margin: 0;
  margin-bottom: 27px;

  font-family: "Hind";
  font-weight: 300;
  line-height: 1.63;
  letter-spacing: 0.4px;
  color: #777777;
}

.news-item__info--link {
  font-size: 17px;
  font-weight: 600;
  line-height: 1.53;
  letter-spacing: 0.4px;
  color: #222222;
}
